#video-div{
	position: fixed;
	left: 0;
	bottom: 0;
	z-index: 100;
	max-width: 540px;
	display: flex;
	align-items: flex-start;
	max-height: 70vh;
	video{
		flex: 1;
		width: 90%;
	}
	.close-span{
		text-align: center;
		width: 30px;
		height: 30px;
		margin: 0;
		padding: 0 5px;
		background: red;
		font-size: 20px;
		font-weight: bold;
		cursor: pointer;
	}
}