main{
  section{
    padding: 30px 0;
    background-color: darkorange;
    h2 {
      margin-bottom: 20px;
      text-align: center;
    }
  }
  .card{
    background-color: orange;
  }
  #section1{
    >div>p{
      text-align: center;
    }
    background-image: linear-gradient(72deg, $secondary-color, $primary-color)!important;
    h3{
      text-align: center;
    }
  }
  #section2{
    .reduced-padding-left{
      padding-left: 0; 
      padding-right: 7.5px;
    }
    .reduced-padding-right{
      padding-right: 0; 
      padding-left: 7.5px;
    }
    @media only screen and (min-width: 768px) and (max-width: 991px) {
      .reduced-padding-left-inmd{
        padding-left: 0; 
        padding-right: 7.5px;
      }
      .reduced-padding-right-inmd{
        padding-right: 0; 
        padding-left: 7.5px;
      }
    }
    @media only screen and (min-width: 768px) {
      .reduced-padding-left-abmd{
        padding-left: 0; 
        padding-right: 7.5px;
      }
      .reduced-padding-right-abmd{
        padding-right: 0; 
        padding-left: 7.5px;
      }
      .no-padding-abmd{
        padding-left: 0;
        padding-right: 0;
      }
    }
  }
  #section3{
    height: 220px;
  }
  #section4{
    .secondary-image{
      border-radius: 0px;
    }
    .no-padding-left{
      padding-left: 0;
    }
    @media only screen and (min-width: 766px){
      .no-padding-left-sm-up{
        padding-left: 0;
      }
      .no-padding-sm-up{
        padding-right: 0;
        padding-left: 0;
      }
    }
  }
  #section5{
    // height: 750px;
    height: 700px;
    background-image: linear-gradient(72deg, $primary-color, $secondary-color)!important;
    .center-column{
      float: none;
      margin: 0 auto;
    }
    .contact-address{
      font-size: 18px; 
      border-radius: 2px;
      background-color: darkorange;
      height: 470px;
      padding-top: 50px;
      text-align: center;
      .contact-buttons{
        padding-top: 10px;
      }
      address{
        margin-bottom: 20px;
        font-style: normal;
        line-height: 1.428571429;
      }
      p span {
        // color: orange;
      }
      p {
        font-weight: 800;
        font-size: 30px;
      }
      button, a{
        margin-bottom: 12px;
      }
    }
  }
  .parallax{
    color: white;
    background: url("../img/20.jpg") no-repeat center center fixed;
    background-size: cover;
  }
}
